import axios from "axios";

const getTrendFilms = () =>
  axios
    .get(
      "https://api.themoviedb.org/3/trending/movie/day?api_key=b8071974481c60cf154163a6bdc34d3f"
    )
    .then((response) => response.data.results);

const getRequestedFilms = (keyWord) =>
  axios
    .get(
      `https://api.themoviedb.org/3/search/movie?api_key=b8071974481c60cf154163a6bdc34d3f&query=${keyWord}`
    )
    .then((response) => response.data.results);

const getRequestedMovie = (movieId) =>
  axios
    .get(
      `https://api.themoviedb.org/3/movie/${movieId}?api_key=b8071974481c60cf154163a6bdc34d3f`
    )
    .then((response) => response.data);

const getFilmReviews = (movieId) =>
  axios
    .get(
      `https://api.themoviedb.org/3/movie/${movieId}/reviews?api_key=b8071974481c60cf154163a6bdc34d3f`
    )
    .then((response) => response.data.results);

const getFilmCast = (movieId) =>
  axios
    .get(
      `https://api.themoviedb.org/3/movie/${movieId}/credits?api_key=b8071974481c60cf154163a6bdc34d3f`
    )
    .then((response) => response.data.cast);

const services = {
  getTrendFilms,
  getRequestedFilms,
  getRequestedMovie,
  getFilmReviews,
  getFilmCast,
};
export default services;
