import React from "react";
import { NavLink, withRouter } from "react-router-dom";
import styles from "../../Styles/marcup.module.css";

const Marcup = ({ films, location }) => {
  return (
    <ul className={styles.marcupList}>
      {films.map(({ title, id }) => (
        <li key={id} className={styles.marcupItem}>
          <NavLink
            to={{
              pathname: `/movies/${id}`,
              state: { from: location },
            }}
            className={styles.link}
          >
            {title}
          </NavLink>
        </li>
      ))}
    </ul>
  );
};

export default withRouter(Marcup);
